.team__container {
    display: grid;
    max-width: 1140px;
    grid-template-columns: repeat(auto-fit, minmax(168px, 1fr));
    grid-auto-rows: minmax(168px, auto);
    grid-gap: 14px;
    justify-content: center;
    padding: 0;
    list-style: none;
    margin-left: auto;
    margin-right: auto;
}

.team__item {
    border-radius: 12px;

    position: relative;
    padding: 10px;
    perspective: 600px;
    text-align: center;
    background-color: #ffffff;
}

.team__item:hover {
    box-shadow: 1px 1px 5px 1px rgba(255, 107, 8, 0.8);
    transform: scale(1.01);
}

.team__image {
    position: relative;
    z-index: 3;
    min-width: 0;
    height: 150px;
    width: 150px;
    border-radius: 50%;
    object-fit: cover;
    margin: 5px auto;
    overflow: hidden;
}

.team__image:hover {
    transition: transform var(--transition);
        transform: scale(1.01);
            box-shadow: 1px 1px 5px 1px rgba(255, 107, 8, 0.8);
}


.team-meta {

 display: flex;
 align-items: center;
 justify-content: center;
 flex-direction: column;
}


.modal {
    position: relative;
    padding: 40px;
    background-color: #e2eff97d;
    box-shadow: 1px 1px 15px 5px var(--footer-color);
    border-radius: 8px;
    transition: transform var(--transition);
    margin-left: auto;
    margin-right: auto;



@include mq(tab) {
        position: absolute;
        width: 760px;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%) scale(1);
    }

  @include mq(desc) {
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%) scale(1.1);
    }
}

.modal__icon {
    padding: 0;
    width: 14px;
    height: 14px;
    fill: var(--footer-color);
}

.modal__icon:hover {
    transition: var(--transition);
        opacity: 0.8;
}


.modal__button {
    position: absolute;
    right: 8px;
    top: 8px;
    cursor: pointer;
    display: flex;
    justify-content: center;
    align-items: center;
    width: 30px;
    height: 30px;
    border-radius: 50%;

    background-color: #3d42457d;
    transition: scale var(--transition);
}

body.modal-open {
    overflow: hidden;
}


.backdrop {
    padding: 20px;
    overflow-y: scroll;

    opacity: 1;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    background-color: #07131bad;
    position: fixed;
    transition: opacity var(--transition);
}

.is-hidden {
    opacity: 0;
    pointer-events: none;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%) scale(1.1);
}

body.modal-open {
    overflow: hidden;
}

.team__item-text {
    font-weight: 400;
        font-size: 12px;
        display: flex;
        align-items: center;
        text-transform: uppercase;
         color: var(--alfavito-color);
        line-height: 1.14;
        font-weight: 500;
        margin-top: 8px;
} 


.team__item-title {
    font-family: 'Roboto';
    font-weight: 400;
    font-size: 12px;
    display: flex;
    align-items: center;
    text-transform: uppercase;
    color: var(--footer-color);
    line-height: 1.14;
    font-weight: 500;
    margin-top: 8px;

}